.hero-container {
  position: relative;
  background-size: cover;
  background-position: center right;
  min-height: 80vh;
  color: $white;
  padding: 140px 0;

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 0; 
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.7) 30%, transparent 100%);
    z-index: 0;
  }

  .wrapper {
    position: relative;
    max-width: 1084px;
    margin: 0 auto;
    z-index: 100;
  }

  .hero-content {
    max-width: 700px;
    margin: 0 40px;

    .hero-heading {
      margin: 0 auto 25px;
      font-size: 50px;
      line-height: 60px;
      letter-spacing: 1px;
    }

    .hero-subheading {
      margin: 0 auto;
      opacity: 0.8;
      font-size: 18px;
      line-height: 27px;
      font-weight: 300;
      letter-spacing: 0.5px;
    }
  }
}

.hero-container-small {
  background-color: $lightBlue;
  color: $darkBlue;
  text-align: center;
  padding: 80px;

  h1 {
    margin: 0;
    padding: 0;
  }
}

@media (max-width: 767px) {
  .hero-container {
    padding: 100px 0;

    .hero-heading {
      font-size: 34px;
      line-height: 42px;
    }

    .hero-subheading {
      font-size: 16px;
    }
  }
}

@media (max-width: 479px) {
  .hero-container {
    padding: 100px 0;

    &:before {
      background: rgba(0, 0, 0, 0.5) 100%;
    }

    .hero-content {
      margin: 0 20px;

      .hero-heading {
        font-size: 40px;
        line-height: 46px;
      }

      .hero-subheading {
        line-height: 24px;
      }
    }
  }
}