// Media queries / Break points
$compact-web: 'all and (min-width:1024px) and (max-width:1430px)';
$tablet-landscape: 'all and (min-width:1024px) and (max-device-width:1430px) and (orientation: landscape)';
$tablet-portrait: 'all and (min-width:768px) and (max-device-width:1023px) and (orientation: portrait)';
$tablet: 'all and (min-width:768px) and (max-width:1023px)';
$mobile-landscape: 'all and (min-width:481px) and (max-device-width:767px)';
$mobile-portrait: 'all and (min-width:128px) and (max-device-width:480px)';
$mobile: 'all and (min-width:128px) and (max-width:767px)';

// Project colours
$grey: #b9b9b9;
$lightGrey: #dddddd;
$white: #ffffff;

$black: #000000;
$green: #9dc850;
$darkGreen: #219653;
$blue: #5bc1ee;
$darkBlue: #0D1949;
$lightBlue: #EEF2FB;

// Project Font colour
$baseFontFamily: 'Montserrat',
sans-serif;

$baseFontColour: $black;
$baseAnchorColour: $black;

$headingFontFamily: 'HelveticaNeue',
Arial,
Verdana,
sans-serif;
