.prefooter,
footer {
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.prefooter {
  background-color: $darkBlue;
  color: $white;

  .container {
    .appstore-container {
      text-align: center;
      margin-top: 20px;

      .appstore-link {
        display: inline-block;
        margin-right: 10px;

        &.last {
          margin-right: 0;
        }
      }
    }

    .social-link {
      display: inline-block;
      margin-right: 15px;
      opacity: 1;
      transition: opacity 200ms ease;
      vertical-align: top;
      
      &:last-child {
        margin-right: 0;
      }
      
      &:hover {
        opacity: 0.6;
        text-decoration: none;
      }

      svg, img {
        width: 20px;
        height: 20px;
        vertical-align: top;
      }
    }
  }
}

footer {
  background-color: #060e2f;
  color: $white;
  padding: 10px 0;

  .container {
    a {
      &:first-child {
        padding-left: 0;
      }
    }

    p {
      margin: 0;
      font-size: 12px;
    }
  }
}

@media (max-width: 767px) {
  .prefooter,
  footer {
    .container {
      flex-direction: column;

      .footer-column {
        margin-bottom: 20px;

        &.last-footer {
          margin-bottom: 0;
        }
      }
    }
  }

  .prefooter {
    padding-bottom: 0;
  }
}

@media (max-width: 479px) {
  .prefooter {
    padding-top: 0;

    .container {
      .appstore-container {
        .appstore-link {
          width: 120px;
          height: auto;
          margin-top: 20px;

          &.last {
            margin-bottom: 0;
          }
        }
      }
    }
  }
}